.style {
    display: grid;
    grid-template-columns: 2fr 10fr;
    width: 100%;
    height: 100%;
}

.auth {
    font-family: "Monotype Corsiva,san-serif";
    font-size: 28px;
    font-style: italic;
    letter-spacing: 10px;
    color: saddlebrown;
    padding: 45px 55px 45px 45px;
}

.credentials {
    display: flex;
    align-items: start;
    flex-direction: column;
    font-style: normal;
    color: black;
}

.email {
    padding: 20px 0px  20px 0;
}